.styled-checkbox {
  position: absolute;
  opacity: 0;

  & + label {
    position: relative;
    cursor: pointer;
    padding: 0;
  }

  // Box.
  & + label:before {
    content: '';
    margin-right: 10px;
    display: inline-block;
    vertical-align: text-top;
    width: 20px;
    height: 20px;
    background: transparent;
    border: 1px solid #6ceec7;
    border-radius: 3px;
  }

  // Box hover
  &:hover + label:before {
    background: #6ceec7;
  }

  // Box checked
  &:checked + label:before {
    background: #6ceec7;
  }

  // Disabled state label.
  &:disabled + label {
    color: #6ceec7;
    cursor: auto;
  }

  // Checkmark. Could be replaced with an image
  &:checked + label:after {
    content: '';
    position: absolute;
    left: 5px;
    top: 9px;
    background: white;
    width: 2px;
    height: 2px;
    box-shadow:
      2px 0 0 white,
      4px 0 0 white,
      4px -2px 0 white,
      4px -4px 0 white,
      4px -6px 0 white,
      4px -8px 0 white;
    transform: rotate(45deg);
  }

  &__text {
    color: #e0e0e0;
  }
}
